.date-current{align-items:center;color:rgba(255,255,255,0.8);display:flex;font-size:18px;font-size:16px;justify-content:center;left:50%;position:absolute;text-align:center;transform:translate(-50%, 80px);width:100%}.date-current__text{opacity:0;transform:translateY(-50px);transition:transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1),opacity 0.3s 0.1s ease-out;will-change:opacity, transform}.date-current__text.animate-in{opacity:1;transform:translateY(0)}
