.gps-location{background-color:transparent;border:none;cursor:pointer;fill:#fff;left:20px;opacity:0;position:absolute;top:20px;transform:translateY(-30px);transition:transform 0.2s cubic-bezier(0.165, 0.84, 0.44, 1),opacity 0.2s ease-out;width:45px;will-change:opacity, transform}.gps-location.animate-in{opacity:1;transform:translateY(0)}.gps-location.press{left:21px;opacity:.6;top:21px}
