.forecast{align-items:center;border-right:1px solid rgba(255,255,255,0.25);display:flex;flex-direction:column;padding-left:5px;padding-right:5px;position:relative;transform:scaleY(0);transform-origin:bottom center;transition:transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);width:58px}.forecast.animate-in{transform:scaleY(1)}.forecast:last-child{border-right:0}.forecast__icon{height:40px;pointer-events:none;width:40px}.forecast__title{margin-bottom:20px;text-transform:capitalize}.forecast__temperature,.forecast__icon,.forecast__title{opacity:0;transform:translateY(-20px);transition:transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1),opacity 0.3s ease-out}.forecast__temperature .temperature__unit-dot{border:1px solid;display:inline-block;height:2px;margin-left:2px;margin-top:4px;position:absolute;width:2px}.forecast__title.animate-in,.forecast__icon.animate-in,.forecast__temperature.animate-in{opacity:1;transform:translateY(0)}
