.navigation{align-items:center;display:flex;height:20px;justify-content:center;margin-bottom:20px;margin-top:10px;width:100%}.navigation__dot{background-color:transparent;border:1px solid #fff;border-radius:6px;height:6px;margin-left:4px;margin-right:4px;opacity:0;transform:translateY(-20px);transition:transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1),opacity 0.3s ease-out,background-color 0.3s ease-out;width:6px}.navigation__dot.animate-in{opacity:1;transform:translateY(0)}.navigation.hourly .navigation__dot:first-child{background-color:#fff}.navigation.hourly .navigation__dot:last-child{background-color:transparent}.navigation.daily .navigation__dot:last-child{background-color:#fff}.navigation.daily .navigation__dot:first-child{background-color:transparent}
