.info{background-color:transparent;box-sizing:border-box;color:transparent;height:100vh;line-height:1.4;overflow:hidden;padding:30px;pointer-events:none;position:absolute;transition:all .2s .05s ease-out;z-index:1}.info ul{list-style:none;margin:0;opacity:0;padding:0;transition:opacity .22s .15s ease-out}.info .link{color:#000;display:block;line-height:inherit;padding:6px 0;text-decoration:none;width:100%}.info .link:hover{color:#2a329e}.info .github{display:block;margin:0 auto;margin-top:50px;opacity:0;text-align:center;transition:opacity .22s .15s ease-out;width:100%}.info h2{font-size:20px;margin-top:50px}.info.animate-in{background-color:#fff;color:#000;pointer-events:unset}.info.animate-in ul{opacity:1}.info.animate-in .github{opacity:1}
