.location{align-items:center;display:flex;font-size:18px;justify-content:center;margin-top:30px}.location__icon{fill:#fff;height:26px;margin-right:6px;opacity:0;transform:translateY(-20px);transition:transform 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55),opacity 0.3s ease-out;will-change:transform, opacity}.location__icon.animate-in{opacity:1;transform:translateY(0)}.location__icon svg{width:14px}.location__text{opacity:0;transform:translateY(20px);transition:transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275),opacity 0.3s 0.1s ease-out;will-change:transform, opacity}.location__text.animate-in{opacity:1;transform:translateY(0)}
